"use client";

import { useState } from "react";
import { Button } from "@/app/components/ui/button";
import {
  Dialog,
  DialogContent,
  DialogDescription,
  DialogHeader,
  DialogTitle,
  DialogTrigger,
} from "@/app/components/ui/dialog";
import { Input } from "@/app/components/ui/input";
import { Label } from "@/app/components/ui/label";
import { Textarea } from "@/app/components/ui/textarea";
import { Plus } from "lucide-react";

interface CreateVotingDialogProps {
  onCreateVoting: (title: string, description: string, options: string[]) => void;
}

export default function CreateVotingDialog({ onCreateVoting }: CreateVotingDialogProps) {
  const [open, setOpen] = useState(false);
  const [title, setTitle] = useState("");
  const [description, setDescription] = useState("");
  const [options, setOptions] = useState<string[]>(["", ""]);

  const handleAddOption = () => {
    setOptions([...options, ""]);
  };

  const handleOptionChange = (index: number, value: string) => {
    const newOptions = [...options];
    newOptions[index] = value;
    setOptions(newOptions);
  };

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    const validOptions = options.filter(opt => opt.trim() !== "");
    if (title && validOptions.length >= 2) {
      onCreateVoting(title, description, validOptions);
      setOpen(false);
      setTitle("");
      setDescription("");
      setOptions(["", ""]);
    }
  };

  return (
    <Dialog open={open} onOpenChange={setOpen}>
      <DialogTrigger asChild>
        <Button>
          <Plus className="mr-2 h-4 w-4" />
          Nowe głosowanie
        </Button>
      </DialogTrigger>
      <DialogContent className="sm:max-w-[525px]">
        <DialogHeader>
          <DialogTitle>Utwórz nowe głosowanie</DialogTitle>
          <DialogDescription>
            Dodaj tytuł, opis i opcje do wyboru
          </DialogDescription>
        </DialogHeader>
        <form onSubmit={handleSubmit} className="space-y-4">
          <div>
            <Label htmlFor="title">Tytuł</Label>
            <Input
              id="title"
              value={title}
              onChange={(e) => setTitle(e.target.value)}
              placeholder="np. Dokąd pojedziemy?"
              required
            />
          </div>
          <div>
            <Label htmlFor="description">Opis (opcjonalnie)</Label>
            <Textarea
              id="description"
              value={description}
              onChange={(e) => setDescription(e.target.value)}
              placeholder="Dodatkowe informacje..."
            />
          </div>
          <div>
            <Label>Opcje do wyboru</Label>
            {options.map((option, index) => (
              <Input
                key={index}
                value={option}
                onChange={(e) => handleOptionChange(index, e.target.value)}
                placeholder={`Opcja ${index + 1}`}
                className="mt-2"
                required={index < 2}
              />
            ))}
            <Button
              type="button"
              variant="outline"
              onClick={handleAddOption}
              className="mt-2 w-full"
            >
              + Dodaj opcję
            </Button>
          </div>
          <div className="flex justify-end gap-2">
            <Button type="button" variant="outline" onClick={() => setOpen(false)}>
              Anuluj
            </Button>
            <Button type="submit">Utwórz głosowanie</Button>
          </div>
        </form>
      </DialogContent>
    </Dialog>
  );
}