"use client";

import { Card, CardContent, CardHeader, CardTitle } from "@/app/components/ui/card";
import { Button } from "@/app/components/ui/button";
import { Progress } from "@/app/components/ui/progress";
import type { Voting } from "../types";

interface VotingListProps {
  votings: Voting[];
  onVote: (votingId: number, optionId: number) => void;
}

export default function VotingList({ votings, onVote }: VotingListProps) {
  if (votings.length === 0) {
    return (
      <Card>
        <CardContent className="pt-6">
          <p className="text-center text-muted-foreground">
            Brak aktywnych głosowań. Utwórz nowe!
          </p>
        </CardContent>
      </Card>
    );
  }

  return (
    <div className="space-y-4">
      {votings.map((voting) => {
        const totalVotes = voting.options.reduce((sum, opt) => sum + opt.votes, 0);
        
        return (
          <Card key={voting.id}>
            <CardHeader>
              <CardTitle>{voting.title}</CardTitle>
              {voting.description && (
                <p className="text-sm text-muted-foreground">{voting.description}</p>
              )}
            </CardHeader>
            <CardContent className="space-y-3">
              {voting.options.map((option) => {
                const percentage = totalVotes > 0 ? (option.votes / totalVotes) * 100 : 0;
                
                return (
                  <div key={option.id} className="space-y-2">
                    <div className="flex justify-between items-center">
                      <span className="font-medium">{option.text}</span>
                      <span className="text-sm text-muted-foreground">
                        {option.votes} głosów ({percentage.toFixed(0)}%)
                      </span>
                    </div>
                    <div className="flex gap-2 items-center">
                      <Progress value={percentage} className="flex-1" />
                      <Button
                        size="sm"
                        onClick={() => onVote(voting.id, option.id)}
                      >
                        Głosuj
                      </Button>
                    </div>
                  </div>
                );
              })}
              <p className="text-xs text-muted-foreground mt-4">
                Łącznie głosów: {totalVotes}
              </p>
            </CardContent>
          </Card>
        );
      })}
    </div>
  );
}